/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid white;
}
.animated {
  max-width: 200px;

}
.animated-section {

  transition: transform, 0.5s;
}
.animated:hover .animated-section {
  background-color: #FF6363;
  color: white;
  animation: belowBounce 1s linear;
}
.vote-section {
  transition: transform, 0.5s;
}
.animated:hover .vote-section {
  background-color: #F8B400;

  animation: aboveBounce 1s linear;
}

@keyframes belowBounce {
  20%, 50%, 80%, to {
    transform: translateX(0);
  }
  40%{
    transform: translateX(-30px);
  }
  70%{
    transform: translateX(-15px);
  }
  90%{
    transform: translateX(-4px);
  }
}
@keyframes aboveBounce {
  20%, 50%, 80%, to {
    transform: translateX(0);
  }
  40%{
    transform: translateX(30px);
  }
  70%{
    transform: translateX(15px);
  }
  90%{
    transform: translateX(4px);
  }
}

